body {
  margin: 0;
  padding: 15px !important;
  font-family: sans-serif;
}

.installDetail {
  max-width: 1100px;
  margin: 0 auto;
  display: block;
}
.installDetail h3 {
  margin-bottom: 0;
}
.installDetail pre {
  border: 1px solid #e8e8e8;
  padding: 10px;
  background: #ececec;
  margin-top: 0;
  -webkit-box-shadow: 2px 2px 2px #e2e2e2;
  box-shadow: 2px 2px 2px #e2e2e2;
  width:100%;
  overflow-x: auto;
}

.installDetail .table {
  width: 100%;
  border: 1px solid #999;
  border-spacing: 0;
}
.installDetail .table tr td, .installDetail .table tr th{border-bottom: 1px solid #999;
  color: #666; border-left: 1px solid #999;
  white-space: normal;
  padding: 5px;}
.installDetail .table tr td:first-child{width: 120px;}


